import "babel-polyfill";
import Vue from 'vue';
import Dashboard from '../../../WebUI/vue/dashboard/index.vue';
import Ajax from '../../../WebUI/vue/ajax';

Vue.use(Ajax, {
    handleUnautorized: true
});

new Vue({
    el: '#app',
	methods: {
		ready() {
			window.fireEvent('DashboardReady');
		}
	},
    components: {
        Dashboard
    }
});
